import React from 'react'
import Kaus from '../images/kaus-nav.png'
import TLC from '../images/tlc-nav.png'
import Nike from '../images/nike-home.svg'
import RK from '../images/rk-home.svg'

const projects = {
	kaus: {
		image: Kaus,
		title: 'Kaus',
		description: 'Case Study of a responsive E-commerce website for the insurance company',
	},
	nike: {
		image: Nike,
		title: 'Nike Training Club',
		description: 'Adding a new feature to Nike Training Club mobile app',
	},
	racekeeper: {
		image: RK,
		title: 'Racekeeper',
		description: 'iOS mobile app for people who participate in running events',
	},
	tlc: {
		image: TLC,
		title: 'TLC',
		description: 'Case Study of a responsive website for an animal shelter',
	},
};

const PrevNext = (props) => {
	const { prev, next } = props;
	const prevProject = projects[prev];
	const nextProject = projects[next];
	return (
		<div className="prevnext">
			<h4>BROWSE OTHER PROJECTS</h4>
			<div className="prevnext-container">
				<div className="prevnext-project">
					<a href={`/projects/${prev}`}>
						<div className="prevnext-nonhover">
							<h3>{prevProject.title}</h3>
							<img src={prevProject.image} alt={prevProject.title}/>
						</div>
						<div className="prevnext-hover">
							<p>{prevProject.description}</p>
						</div>
					</a>
				</div>
				<div className="prevnext-project">
					<a href={`/projects/${next}`}>
						<div className="prevnext-nonhover">
							<h3>{nextProject.title}</h3>
							<img src={nextProject.image} alt={nextProject.title}/>
						</div>
						<div className="prevnext-hover">
							<p>{nextProject.description}</p>
						</div>
					</a>	
				</div>
			</div>
		</div>
	);
}

export default PrevNext